* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
#customNav {
    font-size: 18px;
    padding-top: 1px;
    background-color: rgb(59, 84, 84);
    padding-bottom: 10px;
    /* height: 55px; */
}
.main-nav {
    list-style-type: none;
    display: block;
}
.main-nav-gone {
    display: none;
}
.nav-links, .logo {
    text-decoration: none;
    color:  rgba(255,255,255,0.8);    
}

.main-nav li {
    text-align: center;
    margin: 15px auto;
}

.logo {
    display: inline-block;
    font-size: 22px;
    margin-top: 10px;
    margin-left: 20px;
}

.navbar-toggle {
    position: absolute;
    top: 10px;
    right: 20px;
    cursor: pointer; 
    color: rgba(255,255,255,0.8);
    font-size: 24px;
}

.nav-links:hover, .logo:hover{
    text-decoration: none;
    color: white;
}
@media screen and (min-width: 768px) {
    #customNav {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        padding-bottom: 0;
        -webkit-align-items: center;
                align-items: center;
        /* background-color:rgb(211, 84, 84); */
        background-color: transparent;
        /* height: 55px; */
    }

    .main-nav {
        display: -webkit-flex;
        display: flex;
        margin-right: 30px;
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: flex-end;
                justify-content: flex-end;
        margin-top: 10px;
    }
    .main-nav-gone {
        display: -webkit-flex;
        display: flex;
        list-style-type: none;
        margin-right: 30px;
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: flex-end;
                justify-content: flex-end;
        margin-top: 10px;
    }
    .main-nav li {
        margin: 0;
    }

    .nav-links {
        margin-left: 40px;
    }

    .logo {
        margin-top: 0;
    }

    .navbar-toggle {
        display: none;
    }

    .nav-links:hover, .logo:hover{
        text-decoration: none;
        color: white;
    }
}
